.stockItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  // border-radius: 5px;
  margin: 25px auto;
  position: relative;
  padding: 5px;
  box-shadow: 0 0 5px rgba(#000, 0);
  // border: 1px solid #c7c7c7;
  .stockInner {
    flex-direction: column;
    display: flex;
    justify-content: center;
    // align-items: space-around;
    flex: 1;
    h4 {
      font-size: 20px;
      // color: #0b0b0b;
      margin-bottom: 0px;
    }
    span {
      font-size: 12px;
    }
    .red {
      color: #ba0e0e;
    }
  }
  h4,
  span {
    text-align: center;
    width: 100%;
    display: block;
  }
  .stockType,
  .stockNumber {
    // flex-direction: row;
    align-items: center;
    .stockTypeTitle {
      white-space: nowrap;
      font-size: 8px;
      text-overflow: ellipsis;
    }
  }
  .stockCateGory {
    position: absolute;
    top: -20px;
    right: 0px;
    display: block;
    width: auto;
    background: #e9e9e9;
    min-width: 50px;
    border-radius: 5px;
    font-size: 7px;
  }
}
.stockColumn {
  h4 {
    margin-bottom: 5px;
    font-size: 15px;
  }
  .stockNumber {
  }
}
.App {
  background-color: #dfdfdf;
}
.appColumnWrapper {
  background-color: #bfbfbf;
}
.appColumnWrapper {
  max-height: calc(100vh - 300px);
  overflow: auto;
}
.stockColumn {
  position: relative;
}
.stockColumn-title {
  position: sticky;
  width: 100%;
  // padding: 5px 15px;
  top: 0;
  text-align: center;
  padding: 10px 0;
  z-index: 50;
}
.Unchanged,
.unchanged {
  color: #323232;
}
.Up,
.LimitUp,
.limitup,
.up {
  color: #8a0505;
}
.Down,
.down,
.limitdown,
.LimitDown {
  color: #035c03;
}
.columnStatus {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}
.headerInner {
  max-height: 300px;
  overflow: auto;
}
